import React, {Component} from 'react';
import { DashPdfComponents } from '../lib';
// import { annotationExample } from '../lib/annotationExample';
import { annotationExample } from '../lib/annotationTFCExample';
import {Button, defaultTheme, Provider} from '@adobe/react-spectrum';

class App extends Component {

    componentDidMount() {
        this.loadJS();
    }

    loadJS = () => {
        ////// load the embed API script
        const url = "https://documentcloud.adobe.com/view-sdk/main.js";
        const script = document.createElement("script");
        script.src = url;
        script.async = true;
        document.body.appendChild(script);
    }

    constructor() {
        super();
        this.state = {
            value: ''
        };
        this.setProps = this.setProps.bind(this);
    }

    setProps(newProps) {
        this.setState(newProps);
    }

    render() {

        return (
            <Provider theme={defaultTheme}>
            <div>
                <DashPdfComponents
                    setProps={this.setProps}
                    {...this.state}
                    key = "https://documentcloud.adobe.com/view-sdk-demo/PDFs/Bodea Brochure.pdf"
                    id = 'pdf-view'
                    label = 'pdf-view-label'
                    apiKey = '899d52477b7d4b589f808242e8d36cc3'
                    fileUrl = "https://documentcloud.adobe.com/view-sdk-demo/PDFs/Bodea Brochure.pdf"
                    fileName = "sample pdf.pdf"
                    fileId = '0d07d124-ac85-43b3-a867-36930f502ac6'
                    highlightsArr = {annotationExample}
                />
            </div>
            </Provider>
        )
    }
}

export default App;
